<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="5">
                        <div style="font-size: 20px;height: 37px">
                            媒体库
                        </div>
                    </el-col>
                </el-row>

                <filter-list listType="media" :filter-list="filterList" @filterAllList="filterMediaList"
                             :label-list="labelList"/>

                <div class="am-form-file">
                    <el-button size="mini" type="primary" style="float: left">上传</el-button>

                    <input type="file" multiple ref="myFiles" @change="changeUploadList"
                           accept="audio/mp3, video/mp4, image/jpg, image/png, image/gif"/>

                    <div style="float: right;font-size: 14px">
                        <el-select placeholder="排序" v-model="sort" value="" size="mini" style="width: 150px"
                                   @change="init">
                            <el-option label="更新时间" value="1"/>
                            <el-option label="英文字幕长度正序" value="2"/>
                            <el-option label="英文字幕长度倒序" value="3"/>
                        </el-select>
                    </div>
                </div>

                <media-list table-type="media" :media-list="mediaList" :current-page="currentPage"
                            @delMediaInfo="delMediaInfo"/>
            </el-main>

            <el-footer style="text-align: center">
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="paginationSize"
                        layout="total, prev, pager, next"
                        :total="paginationNum">
                </el-pagination>
            </el-footer>

            <el-dialog
                    title="上传文件"
                    :visible.sync="dialogUploadVisible"
                    width="50%" class="dialogUpload">
                <div style="border: 1px solid #bbb;min-height: 200px">
                    <div style="padding: 10px;">
                        <div v-for="(i,index) in fileList" :key="index">
                            <el-row type="flex" justify="space-between">
                                <el-col :span="18">
                                    <div>
                                        <span style="font-size: 12px">{{i.name}}</span>
                                        <el-progress :stroke-width="3" :percentage="i.num"
                                                     :status="i.num === 100 ? 'success' : 'text'">{{i.num}}%
                                        </el-progress>
                                    </div>
                                </el-col>
                                <el-col :span="4">
                                    <div>
                                        <el-button v-if="i.id!==''" type="text" @click="jumpNewPage(i.id)"
                                                   icon="el-icon-edit">查看
                                        </el-button>
                                    </div>
                                </el-col>
                            </el-row>
                        </div>
                    </div>
                </div>
                <span slot="footer">
                        <el-button type="primary" @click="hideUploadDia">完成</el-button>
                </span>
            </el-dialog>
        </el-container>
    </el-container>
</template>

<script>
    import FilterList from '../components/FilterListItem'
    import LeftAside from '../components/LeftAsideItem'
    import MediaList from '../components/MediaListItem'

    export default {
        components: {
            FilterList,
            LeftAside,
            MediaList
        },
        data() {
            return {
                mediaList: [],
                multipleSelection: [],
                currentPage: 2,
                isShowSetTime: false,
                fileList: [],
                dialogUploadVisible: false,
                paginationNum: 0,
                filterList: {},
                paginationSize: 20,
                labelList: [],
                sort: '1',
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                this.getMediaListData();
                this.getAllLabelData();
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.jumpPage(`/mediaList#${val}`);
            },
            jumpPage(key) {
                this.$router.push(key)
            },
            handleCommand(command) {
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                }
            },
            hideUploadDia() {
                this.fileList = [];
                this.dialogUploadVisible = false
            },
            getMediaListData(find) {
                find = find ? find : {};
                this.getMediaNumData(find).then(() => {
                    find.page = this.currentPage;
                    find.num = this.paginationSize;
                    this.getSort(find);

                    this.$axios.get('/material/_find', {params: find}).then((res) => {
                        console.log(res.data.data)
                        if (!res.data.code) {
                            this.mediaList = res.data.data
                        }
                    });
                });
            },
            getSort(data) {
                if (this.sort === '2') {
                    data.sort = {enVvtLen: -1};
                } else if (this.sort === '3') {
                    data.sort = {enVvtLen: 1};
                }
                return data
            },
            handleUploadChange() {
                this.dialogUploadVisible = true;
            },
            filterMediaList(list) {
                let data = {
                    page: 1,
                    // enTitle: list.title || '',
                    // zhTitle: list.title || '',
                    // abstract: list.title || '', //摘要
                    // enVvt: list.title || '', //英文字幕
                    // zhVvt: list.title || '', //中文字幕
                };

                if (list.label) {
                    data.label = list.label;
                }
                if (list.category) {
                    data.category = list.category;
                }
                if (list.difficulty !== 'all') {
                    data.difficulty = list.difficulty;//难度
                }
                if (list.stage !== 'all') {
                    data.stage = list.stage;
                }
                this.getMediaListData(data);
            },
            getMediaNumData(find) {
                return new Promise((resolve, reject) => {
                    this.$axios.get('/material/_getnum', {params: find}).then((res) => {
                        this.paginationNum = res.data.data;
                        this.currentPage = Number(this.$route.hash.substr(1)) || 1;
                        resolve()
                    })
                })
            },
            getAllLabelData() {
                this.$axios.get('/label/_find').then((res) => {
                    if (!res.data.code) {
                        console.log(res.data.data)
                        this.labelList = res.data.data
                    }
                })
            },
            changeUploadList() {
                let fileList = Object.values(this.$refs.myFiles.files);

                fileList.forEach(value => {
                    this.fileList.push({name: value.name, num: 0, id: ''});
                });
                this.postUploadData(0, fileList);
            },
            postUploadData(num, fileList) {
                let data = new FormData();
                data.append('file', fileList[num]);
                this.handleUploadChange();

                this.$axios.post('/file', data, {
                    onUploadProgress: function (progressEvent) {
                        this.fileList[num].num = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total));
                    }.bind(this)
                }).then((res) => {
                    if (res.data.success) {
                        this.$message.success('上传成功');
                        this.addMaterial(fileList, num, res);
                    }
                })
            },
            addMaterial(fileList, num, res) {
                this.postMaterialData(this.getMaterialInfo(fileList[num].name, res.data.data.id)).then((result) => {
                    console.log(result);
                    this.fileList[num].id = result.data.data._id;
                    this.getMediaListData();
                    num += 1;
                    if (num <= fileList.length - 1) {
                        this.postUploadData(num, fileList);
                    } else {
                        this.$refs.myFiles.value = '';
                    }
                });
            },

            getMaterialInfo(name, id) {
                // let data = new FormData();
                // data.append('source', 'upload');
                // data.append('source_id', id);
                // data.append('file', JSON.stringify({
                //     r: '',
                //     file_id: id,
                //     type: this.getMediaType(name),
                // }));
                let data = {
                    source: 'upload',
                    source_id: 'id',
                    file: {
                        r: '',
                        file_id: id,
                        type: this.getMediaType(name),
                    }
                };
                return data
            },
            getMediaType(name) {
                let str = name.substr(name.length - 3);
                let type = {
                    'mp4': 'video',
                    'mp3': 'audio',
                    'jpg': 'pic',
                    'png': 'pic',
                    'gif': 'pic',
                };
                return type[str]
            },
            postMaterialData(data) {
                return new Promise((resolve, reject) => {
                    this.$axios.post('/material', data,{headers:{'Accept': 'application/json', 'Content-Type': 'application/json'}}).then((res) => {
                        if (res.data.success) {
                            resolve(res)
                        }
                    });
                })
            },
            jumpNewPage(key) {
                window.open('#/editMediaInfo/' + key, '_blank')
            },

            delMediaInfo(id) {
                this.$confirm('文件将被直接删除,可能导致引用该媒体的文档失效。', '确定删除？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    confirmButtonClass: 'el-button el-button--danger'
                }).then(() => {
                    this.delMediaData(id)
                }).catch(() => {
                    // this.$message({type: 'info', message: '已取消删除'});
                });
            },
            delMediaData(id) {
                this.$axios.get('/material/_delete', {params: {materialId: id}}).then((res) => {
                    if (!res.data.code) {
                        this.$message({type: 'success', message: '已删除'});
                        this.init();
                    }
                })
            },
        },
        watch: {
            $route() {
                this.init();
            }
        },
    }
</script>

<style scoped>
    .am-form-file {
        position: relative;
        overflow: hidden;
    }

    .am-form-file input[type=file] {
        position: absolute;
        z-index: 1;
        left: 0;
        opacity: 0;
        width: 56px;
        height: 28px;
        cursor: pointer;
    }
</style>